<template>
    <div>
        <div class="row mt-3">
            <div class="col">
                <h3>
                    {{ topic.topic }}
                </h3>
            </div>
        </div>
        <div class="row">
            <div class="col" v-if="comment && !$root.is_owner">
                HardlyDifficult said "{{ comment }}"
            </div>
        </div>
        <div class="row mt-1" v-if="!$root.no_account_found && !show_tip">
            <div class="col">
                <button @click="show_tip = true" class="btn btn-secondary">Tip This Topic</button>
            </div>
        </div>
        <div class="row justify-content-center" v-if="show_tip">
            <div class="card mb-3 text-center">
                <div class="modal-header text-white bg-secondary">
                    <h6 class="modal-title">
                        Tip This Topic
                    </h6>
                    <button type="button" class="close" @click="show_tip = false" aria-label="Close">
                        <span aria-hidden="true" class="x">&times;</span>
                    </button>
                </div>
                <div class="card-body">
                    <span class="card-text">
                        <div class="row">
                            <input type="number" v-model="value" :min="$root.min_for_existing_topic" step="0.01"/> ETH
                        </div>
                        <div class="row mt-1">
                            ({{ $root.min_for_existing_topic }} ETH minimum)
                        </div>
                        <div class="row mt-3">
                            <button @click="add()" class="btn btn-primary">Add Tip</button>
                        </div>
                    </span>
                </div>
            </div>
        </div>
        <div class="row mt-1">
            <div class="col">
                Total Pledged {{ topic.total_support }} ETH
                (from {{ topic.supporter_count }} supporter<span v-if="topic.supporter_count>1">s</span>)
                <span v-if="topic.my_contribution > 0">
                    My Contribution: {{ topic.my_contribution }}
                    <button @click="refund()">Refund</button>
                </span>
            </div>
        </div>
        <div class="row mt-3">
            <div class="col" v-if="$root.is_owner">
                <textarea v-model="comment" />
                <button @click="postComment()">Comment</button>
                <button @click="accept()">Accept</button>
                <button @click="decline()">Decline</button>
            </div>
        </div>
    </div>
</template>

<script>
import contract from "../js/contract.js";
import neb from "../js/nebcontract.js";
import eth from "../js/eth.js";

export default 
{
    props : ['topic'],
    data()
    {
        return {
            comment: "",
            value: this.$root.min_for_existing_topic,
            show_tip: false,
        }
    },
    methods:
    {
        async accept()
        {
            await contract.accept(this.topic.topic, this.$root.onTxPosted);
            this.$root.onTxComplete();
        },
        async add()
        {
            await contract.requestTopic(this.topic.topic, this.value, this.$root.onTxPosted);
            this.$root.onTxComplete();
        },
        async decline()
        {
            await contract.decline(this.topic.topic, this.$root.onTxPosted);
            this.$root.onTxComplete();
        },
        async refund()
        {
            await contract.refund(this.topic.topic, this.$root.onTxPosted);
            this.$root.onTxComplete();
        },
        async postComment()
        {
            await neb.setMessage(this.topic.topic, this.comment, this.$root.onTxPosted, this.onError);
            this.$root.onTxComplete();
        },
        onError()
        {
            // TODO
        }
    },
    async mounted()
    {
        this.comment = await neb.getMessage(this.topic.topic);
    },
}
</script>
<style scoped>
.x
{
    color: white;
    text-shadow: none;
    font-weight: normal;
}
.close
{
    opacity: 1; 
}
</style>


